<template>
<div class="page w-full h-screen">
      <Header></Header>
    <div id="modal" class="w-full flex h-full pt-20 justify-center">
      <ul class="w-full text-3xl leading-10 list-disc pl-10">
        <form v-on:submit.prevent="addTask">
          <input class="border-2 rounded-lg shadow-lg p-3" type="text" v-model="newTaskName" placeholder="新しいタスク">
        </form>
        <div v-for="task in tasks" :key="task.id">
        <li v-if="!task.done" class="py-5 border-b border-solid border-gray-200">
          <el-button @click="openModal(task.id)" class="float-right" type="warning">できた！</el-button>
          {{ task.name }}
        </li>
        </div>
      </ul>
      <div id="overlay" v-show="showContent" @click="closeModal" class="w-full h-full fixed flex items-center justify-center">
        <div id="content" class="w-1/2 bg-white rounded-lg">
          <p>おつかれさま！</p>
          <p><button @click="closeModal" class="px-1 border-green-400 border-2 rounded-lg float-right">Close</button></p>
        </div>
      </div>
        <Footer></Footer>
    </div> 
</div>
</template>


<script>

import Header from '@/components/Header.vue'
import Footer from '@/components/Footer.vue'

export default {
  name:'modal',
  data() {
    return {
      newTaskName: '',
      showContent:false,
      taskId: '',
      finishId: '',
      id: ''
    }
  },

watch: {
  // id(newTask) {
  //   localStorage.task.id = newTask;
  // }
},
  methods: {
    addTask () {
    this.$store.commit('addTask', {
      name: this.newTaskName,
    })
    this.newTaskName =''
    localStorage.setItem('taskdata', JSON.stringify(this.tasks))
    },
    toggleTaskStatus (task) {
      this.$store.commit('toggleTaskStatus', {
        id: task.id
      })
    },
    save () {
      this.$store.dispatch('save')
    },
    restore () {
      this.$store.dispatch('restore')
    },
    openModal(id) {
      this.showContent = true
      this.taskId = id;
      
      // this.addTask =false
    },
    closeModal () {
      this.finishId = this.taskId;
      this.showContent = false
      this.$store.commit('delTask', this.taskId)
      localStorage.setItem('taskdata', JSON.stringify(this.tasks))
    }
  },
  computed: {
    tasks () {
      return this.$store.state.tasks
    }
  },
    components: {
    Header,
    Footer
  }
};
</script>

<style>
#overlay {
  z-index:1;
  top: 0;
  left: 0;
  background-color:rgba(0,0,0,0.5);
}
#content {
  z-index: 2;
  padding: 1em;
}
</style>